@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
:root {
  --dkModeElems: hsl(209, 23%, 22%);
  --dkModeBg: hsl(207, 26%, 17%);
  --ltModeText: hsl(200, 15%, 8%);
  --ltModeInput: hsl(0, 0%, 52%);
  --ltModeBg: hsl(0, 0%, 98%);
  --dkModeText: hsl(0, 0%, 100%);
  --boxShadow: 0 0 10px -5px var(--ltModeInput);
  --boxShadowDk: 0 0 10px -5px var(--ltModeText);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Nunito Sans", sans-serif;
  background-color: hsl(0, 0%, 98%);
  background-color: var(--ltModeBg);
  color: hsl(200, 15%, 8%);
  color: var(--ltModeText);
}

header {
  width: 100%;
  height: 5rem;
  display: -webkit-flex;
  display: flex;
  place-items: center;
  background-color: white;
  box-shadow: 0 0 10px -5px hsl(0, 0%, 52%);
  box-shadow: var(--boxShadow);
}

.header__container {
  font-weight: 800;
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 90%;
}

.header__container > p {
  font-size: 1.25rem;
}

.theme {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 0.375rem;
  gap: 0.375rem;
}

.theme i {
  cursor: pointer;
}

/*End of Header*/

.container {
  margin: 1.5rem auto;
  width: 90%;
  position: relative;
}

.inputs {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  grid-gap: 2.5rem;
  gap: 2.5rem;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.search-container {
  width: clamp(15rem, 100%, 30rem);
  height: 3.125rem;
  position: relative;
}

.input {
  border-style: none;
  border-radius: 0.313rem;
}

.input-search {
  width: clamp(15rem, 100%, 30rem);
  height: 3.125rem;
  padding-left: 4.688rem;
  box-shadow: 0 0 10px -5px hsl(0, 0%, 52%);
  box-shadow: var(--boxShadow);
}

.input-search::-webkit-input-placeholder {
  opacity: 0.6;
}

.input-search:-ms-input-placeholder {
  opacity: 0.6;
}

.input-search::placeholder {
  opacity: 0.6;
}

.fa-search {
  position: absolute;
  top: 50%;
  left: 2.03rem;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  color: hsl(0, 0%, 52%);
  color: var(--ltModeInput);
  opacity: 0.5;
}

.select-container {
  position: relative;
  width: 12.5rem;
}

.input-filter {
  width: 12.5rem;
  position: relative;
  height: 3.125rem;
  padding: 0 0 0 1.25rem;
  -webkit-appearance: none;
  appearance: none;
  box-shadow: 0 0 10px -5px hsl(0, 0%, 52%);
  box-shadow: var(--boxShadow);
}

.select-container::after {
  font-family: "Font Awesome 5 Free";
  content: "\f078";
  font-weight: 900;
  top: 50%;
  right: 1rem;
  position: absolute;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

/*cards*/

.cards-container {
  width: 100%;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
}

@media screen and (min-width: 43.75rem) {
  .cards-container {
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
  }
}
@media screen and (min-width: 60rem) {
  .cards-container {
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
}

.card {
  width: 17rem;
  height: 22rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  border-radius: 5px;
  margin-top: 2.5rem;
  overflow: hidden;
  cursor: pointer;
}

.card-flag {
  width: 100%;
  height: 10rem;
  object-fit: cover;
}

.card-info {
  height: 12rem;
  padding: 1.563rem;
  background-color: white;
}

h2 {
  font-size: 1rem;
  margin-bottom: 1.563rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100%;
}

h3 {
  font-size: 0.875rem;
  margin-bottom: 0.5rem;
}

span {
  font-weight: lighter;
}

.page-container {
  padding: 2.5rem 5vw 3.75rem;
  margin: 0 auto;
}

.page-container button {
  min-width: 5.938rem;
  box-shadow: 0 0 10px -5px hsl(0, 0%, 52%);
  box-shadow: var(--boxShadow);
  background-color: hsl(0, 0%, 100%);
  background-color: var(--dkModeText);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: 300;
}

.back-btn {
  width: 6.75rem;
  height: 2.2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
  font-weight: 300;
  font-family: "Nunito Sans", sans-serif;
}

.back-btn i {
  font-size: 1.5rem;
}

.info-container {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.info-container h2 {
  margin: 0;
}

.info-flag {
  width: clamp(10rem, 100%, 35rem);
  height: auto;
  margin: 4.688rem 0 3.125rem;
  object-fit: cover;
}

.info-info {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  grid-gap: 2rem;
  gap: 2rem;
  -webkit-justify-content: center;
          justify-content: center;
}

.info-one {
  width: 20rem;
}

.info-two {
  width: 20rem;
}

.border-container {
  width: 20rem;
}

.border {
  font-weight: 600;
  font-size: 1.275rem;
}

.border-buttons {
  width: 100%;
  height: 1.875rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
}

.border-btn {
  height: 1.875rem;
  padding: 0 1rem;
  font-weight: 300;
  font-size: 0.75rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

body.dark {
  background-color: hsl(207, 26%, 17%);
  background-color: var(--dkModeBg);
  color: hsl(0, 0%, 100%);
  color: var(--dkModeText);
}

body.dark header,
body.dark .page-container button,
body.dark .input {
  background-color: hsl(209, 23%, 22%);
  background-color: var(--dkModeElems);
  color: hsl(0, 0%, 100%);
  color: var(--dkModeText);
  box-shadow: 0 0 10px -5px hsl(200, 15%, 8%);
  box-shadow: var(--boxShadowDk);
}

body.dark .input-search::-webkit-input-placeholder {
  background-color: hsl(209, 23%, 22%);
  background-color: var(--dkModeElems);
  color: hsl(0, 0%, 100%);
  color: var(--dkModeText);
  opacity: 1;
}

body.dark .input-search:-ms-input-placeholder {
  background-color: hsl(209, 23%, 22%);
  background-color: var(--dkModeElems);
  color: hsl(0, 0%, 100%);
  color: var(--dkModeText);
  opacity: 1;
}

body.dark .fa-search,
body.dark .input-search::placeholder,
body.dark .card-info {
  background-color: hsl(209, 23%, 22%);
  background-color: var(--dkModeElems);
  color: hsl(0, 0%, 100%);
  color: var(--dkModeText);
  opacity: 1;
}

@media screen and (min-width: 48.75rem) {
  h2 {
    width: 42rem;
  }
}
@media screen and (min-width: 72.375rem) {
  h2 {
    width: 100%;
  }
}

@media screen and (min-width: 75rem) {
  .info-container {
    -webkit-justify-content: space-around;
            justify-content: space-around;
    height: 25rem;
    margin: 4.688rem 0 3.125rem;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: initial;
            align-items: initial;
  }

  .info-flag {
    margin: 0;
  }

  .info-one,
  .info-two,
  h2 {
    width: 25rem;
  }

  .border-container {
    display: -webkit-flex;
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    grid-column: 1/3;
    width: 25rem;
  }

  .border-buttons {
    grid-gap: 1rem;
    gap: 1rem;
    width: 100%;
  }
}

@media screen and (min-width: 85rem) {
  .info-info {
    display: grid;
    grid-template-columns: repeat(2, 20rem);
    grid-gap: 0;
    gap: 0;
    -webkit-align-content: space-around;
            align-content: space-around;
  }

  .info-one {
    grid-column: 1/2;
  }

  .border-container {
    width: 100%;
  }
  .border-buttons {
    width: 100%;
  }
}

